.header {
    padding: 55px;
    text-align: center;
}

.header-name {
    font-size: xx-large;
    color: turquoise;
}

.header ul, li {
    list-style: none;
    padding: .5rem 1rem;
    display: inline;
    font-size: x-large;
}

.header a:hover {
    color: royalblue;
    text-decoration: none;
}